@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    letter-spacing: 0.5px;
}

body {
    font-family: "Poppins", sans-serif;
}

p {
    /*color: #696969;*/
    font-size: 12px;
}

b {
    color: #8a8a8a;
}

h5 {
    color: #616161;
}

.pointer {
    cursor: pointer;
}

.myRoot {
    height: 100vh;
    background-color: #293879;
}

button {
    color: white;
    padding: 10px 35px;
    border-width: 0px;
    border-radius: 5px;
    background-color: #293879;
    text-transform: capitalize;
    font-size: 12px;
    cursor: pointer;
}

button:hover {
    color: white;
    background-color: #465185;
}

.dflex {
    display: flex;
    justify-content: space-between;
}

.none {
    display: none;
}

/* Mobile devices -------- */

@media(max-width: 991px) {
    
}

/* Desktop devices -------- */

@media(min-width: 992px) {
    .myRoot {
        display: flex;
    }
}