.sideBar {
    color: white;
    height: inherit;
    background-color: inherit;
    padding: 15px 0px 10px 15px;
    transition-duration: 0.5s;
}

.logo img {
    height: 40px;
    margin-bottom: 5px;
}

.menu {
    margin-top: 20px;
}

.menu div {
    display: flex;
    align-items: center;
    padding: 15px 0px 15px 15px;
    cursor: pointer;
}

.menu div:hover {
    color: white;
    background-color: #465185;
    border-top-left-radius: 7px;
    border-bottom-left-radius: 7px;
}

.menu div p {
    font-size: 13px;
}

.menu div img {
    margin-right: 20px;
}

.menu .selectedItem {
    color: black;
    background-color: white;
    border-top-left-radius: 7px;
    border-bottom-left-radius: 7px;
}


/* Mobile devices -------- */

@media(max-width: 991px) {
    .sideBar {
        position: absolute;
        width: 100%;
        left: -100%;
    }

    .logo {
        text-align: center;
    }
}

/* Desktop devices -------- */

@media(min-width: 992px) {
    * {
        overflow-y: hidden;
    }
    
    .sideBar {
        width: 245px;
    }

    .logo {
        padding-right: 15px;
        margin-bottom: 25px;
    }

    /* ------- */

    .bottomMessage {
        width: 180px;
        padding: 10px;
        position: absolute;
        bottom: 0;
        background-color: #1C2964;
        border-radius: 7px;
        transition-duration: 1s;
        overflow: visible;
    }

    .bottomMessage .right {
        position: absolute;
        width: 40px;
        top: -20px;
        left: 40%;
    }

    .bottomMessage .cancel {
        cursor: pointer;
        position: absolute;
        width: 15px;
        right: 10px;
    }

    .bottomMessage p {
        font-size: 8px;
    }
}