.mainBody {
    flex-grow: 1;
    align-self: center;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: white;
}

.verticalLine {
    margin: 0px 15px;
    padding: 12px 0px;
    border-left: 2px solid #cccccc;
}

.investor {
    padding: 3px 5px;
    display: flex;
    align-items: center;
    border: 1px solid #cccccc;
    border-radius: 7px;
}

.investor div {
    background-color: #293879;
    padding: 3px 6px;
    margin-right: 10px;
    border-radius: 5px;
}

.investor img {
    height: 15px;
}

.investor p {
    color: #293879;
    font-weight: bold;
}

.green {
    color: #05CF5B;
}


/* --------- */


.body .card {
    border: 1px solid #cccccc;
    border-radius: 7px;
    margin: 10px 0px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
}

.body .card .cardTitle {
    color: #293879;
    font-weight: bold;
    padding: 10px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    background-color: #6E89FF1A;
}

.body .card .cardBody {
    padding: 10px;
}

.body .card .cardBody > * {
    margin: 5px 0px;
}

.body .card .cardBody div {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.numberInput {
    width: 50%;
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 7px;
    border-style: solid;
    border-width: 1px;
    border-color: #cccccc;
}

.profileDiv div {
    display: flex;
    padding: 10px;
    cursor: pointer;
}

.profileDiv div:hover {
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.1);
}

.profileDiv img {
    width: 15px !important;
    height: 15px !important;
    margin-right: 15px;
}

.barCodeDiv {
    position: absolute;
    padding: 5px 5px;
    border-radius: 5px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
}


/* Assests ---- */


.asset {
    display: flex;
    margin: 5px 0px;
    padding-top: 5px;
    cursor: pointer;
}

.asset:hover {
    background-color: rgba( 0, 0, 0, 0.05 );
    border-radius: 7px;
}

.asset img {
    width: 110px;
}

.asset div {
    width: 300px;
}

.end {
    margin-top: 5px;
    cursor: pointer;
}

.end:hover {
    text-decoration: underline;
}

.end h5 {
    color: #293879;
}

/* Mobile devices -------- */

@media(max-width: 991px) {
    .upperBarMobile1 {
        align-items: center;
        background-color: #293879;
        padding: 15px 10px 5px 10px;
    }

    .upperBarMobileInner2 {
        width: 100px;
    }

    .upperBarMobile2 {
        padding: 10px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
    }

    .upperBarMobileInner3 {
        align-items: center;
    }

    .body {
        padding: 15px;
    }

    .profileDiv {
        position: absolute;
        top: 0;
        right: -100%;
        width: 100%;
        height: 100vh;
        background-color: white;
        padding: 15px;
        transition-duration: 0.5s;
    }

    .barCodeDiv {
        top: 90px;
    }

    /* -------- */

    .transactionsBody {
        margin: 15px 0px;
    }

    .transactionsBody > * {
        margin: 5px 0px;
    }

    .transactionsBody div {
        display: flex;
    }

    .transactionsBody .transactionHeading {
        width: 50px;
        color: #8a8a8a;
        font-weight: bold;
    }

    .cardTransaction {
        box-shadow: 0 4px 8px 0 rgba( 0, 0, 0, 0.1 );
        border-radius: 7px;
        padding: 10px;
    }

    /* Assest ------- */

    .asset {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}

/* Desktop devices -------- */

@media(min-width: 992px) {
    .mainBody {
        height: 96vh;
        padding: 10px 25px;
        margin-right: 12px;
        border-radius: 7px;
    }

    .body {
        /*width: 100%;*/
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding: 20px 0px;
    }
    
    .body .bodyInner1 {
        width: 55%;
    }
    
    .body .bodyInner2 {
        width: 40%;
    }
    

    .body .cardDiv {
        display: flex;
        justify-content: space-between;
    }

    .body .card {
        width: 47%;
    }


    .upperBar {
        display: flex;
        justify-content: space-between;
        border-bottom: 1.5px solid #cccccc;
    }
    
    .upperBarInner1 {
        width: 250px;
        display: flex;
        justify-content: space-between;
    }
    
    .upperBarInner1 p {
        color: #b2b2b2;
        padding: 12px 0px;
        font-weight: bold;
        cursor: pointer;
    }
    
    .selectedItem {
        color: #293879 !important;
        border-bottom: 3px solid #293879;
    }
    
    .upperBar .upperBarInner2 {
        width: 430px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    .upperBar .upperBarInner2 img {
        width: 20px;
    }

    .profileLink {
        overflow: hidden;
    }

    .profileLink img {
        transition-duration: 0.5s;
    }

    .profileDiv {
        position: absolute;
        right: 50px;
        padding: 5px 5px;
        border-radius: 5px;
        background-color: white;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.5);
    }

    .barCodeDiv {
        top: 50px;
    }

    /* ------ */

    .transactionsHeader {
        display: flex;
    }

    .transactionsHeader .heading1, .transactionsBodyText1 {
        width: 125px;
    }

    .transactionsHeader .heading2, .transactionsBodyText2 {
        flex-grow: 1;
    }

    .transactionsHeader div {
        font-weight: bold;
    }

    .transactionsBody {
        display: flex;
        margin: 10px 0px;
    }

    /* -------- */

    .bodyInner2 {
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
        border-radius: 7px;
        padding: 10px;
        margin-top: 20px;
    }

    /* Assets ------- */

    .asset img {
        margin-right: 7px;
    }
}